import React, { Component, Suspense, memo } from "react";
import { CloudUrl } from "../../../constants";
import { connect } from 'react-redux';
import './scss/SocialBanner.scss' 
import FilterNetworks from "../../Components/Filters/FilterType/Elements/FilterNetworks";
import { filterPostDataAppendWebFilter } from "../../../actions/filterpostAction";

const errorImage = (name, txtColor, bgColor) => `https://ui-avatars.com/api/?name=${name}&background=${bgColor}&color=${txtColor}&length=1`


const BrandImage = memo(({ LogoImage, title, networkID }) => <div className="tb_sb__brand" key={`network_image_${networkID}`}>
       <div className="tb_sb__brand_icon" >
              <img loading="lazy" className="tb_brand_img" height={64} width={64} src={LogoImage} alt="" onError={e => e.target.src = errorImage(title, "fff", "000")} />
       </div>
</div>)

const linkAdd = (networkId, urllink) => {
       const link = document.createElement('link');
       link.id = `link_${networkId}`;
       link.rel = 'stylesheet';
       link.type = 'text/css';
       link.href = `${CloudUrl}/web-fonts/${urllink}.css`
       document.head.appendChild(link);
}

class SocialBanner extends Component {
       constructor(props) {
              super(props);
              this.state = {
                     networkID: 0,
                     data: null,
                     isOpen: false
              }
              this.dropdownRef = React.createRef();
       }
       toggleDropdown = () => {
              this.setState({
                     isOpen: !this.state.isOpen
              });
       }
       componentDidMount() {
              const { Banner } = this.props;
              this.onUpdate(Banner?.[0]?.networkId || this.state.networkID)
              document.addEventListener('click', this.handleClickOutside);
       }

       componentWillUnmount() {
              document.removeEventListener('click', this.handleClickOutside);
       }

       onUpdate = (networkID) => {
              const { Banner } = this.props;
              const data = Banner.find((item) => item.networkId === networkID);
              if (data) {
                     linkAdd(networkID, data?.title_link_font)
                     this.setState({ data, networkID });
              }
       };
       onClickWebFilters = (networkID) => event => {
              const { appData } = this.props
              if (appData) {
                     const { wall, postData } = appData
                     const appendData = postData?.appendData
                     const tstamp = Math.floor(Date.now() / 1000);
                     let postCount = wall.ThemeRule.numberOfPosts;
                     this.props.filterPostDataAppendWebFilter(
                            wall.Wall.id,
                            tstamp,
                            postCount,
                            networkID,
                            appendData?.after,
                            appendData?.heightEvent,
                            null
                     );
                     this.onUpdate(networkID)
              }
       }
       handleClickOutside = (event) => {
              if (this.dropdownRef.current && !this.dropdownRef.current.contains(event.target)) {
                     this.setState({
                            isOpen: false
                     });
              }
       }
       render() {
              const { Banner } = this.props;
              const { data, networkID, isOpen } = this.state
              return data && Object.keys(data).length > 0 ? <div className="tb_sb__b_cont" key={`network_${networkID}`} style={{ backgroundColor: data?.background_color }}>
                     <div className="tb_sb__b_wrapper">
                            <div className="tb_sb__profile">
                                   <BrandImage LogoImage={data?.LogoImage} title={data?.title} networkID={networkID} />
                                   <div className="tb_sb___info tb-cTBfont-regular" style={{ fontFamily: data?.title_css_font, }}>
                                          <div className="tb_sb__name" aria-label={data?.title} style={{ color: data?.title_font_color, fontSize: parseInt(data?.title_font_size) }}>{data?.title}</div>
                                          <div className="tb_sb__posts" aria-label={data?.subTitle} style={{ color: data?.title_font_color, fontSize: parseInt(data?.subTitle_font_size) }}>{data?.subTitle}</div>
                                   </div>
                            </div>

                            <div className="tb_sb_btn_wrap_" ref={this.dropdownRef}>
                                   <div className={`tb_sb_f_btn tb-cTBfont-`} onClick={data?.networkId != 0 ? event => window.open(data?.followLink, '_blank') : this.toggleDropdown} style={{ backgroundColor: data?.background_color_two, color: data?.social_icon_color, borderColor: data?.background_color_two }}>{data?.subtitle_font_varient}</div>
                                   {isOpen && (
                                          <div className={`tb_sb_dropdown_ ${isOpen ? 'tb_sb_dd_show' : ''}`}>
                                                 {Banner.map((item, index) => item?.networkId != 0 ? 
                                                 <div key={index} className="tb_sb_dd_list" onClick={event => Banner && Banner[index].followLink ? window.open(Banner[index].followLink, "_blank") : null}>
                                                        {item?.networkName}
                                                 </div> : null)}
                                          </div>)}
                            </div>
                     </div>
                     {Banner?.length > 1 ? <div className="tb_sb_f_wrapper">
                            {Banner.map((item, index) => <div className={`tb_sb_f_list ${item?.networkId == networkID ? `tb_active` : ``}`} key={index} tb-data-network={item?.networkId} onClick={this.onClickWebFilters(item?.networkId)}>
                                   <div className="tb_sb_f_button tb-network-rectangular-ico-users">
                                          {item?.networkId != 0 ? <Suspense fallback={<></>}><FilterNetworks filterIconClass={'tb_sb_f_ico'} network={item?.network} isDefault={false} /></Suspense> : null}
                                          <div className="tb_sb_f_button_text">{item?.networkId == 0 ? `All` : item?.networkName}</div>
                                   </div>
                            </div>)}
                     </div> : null}
              </div> : null
       }
}


const mapStateToProps = state => {
       return {
              appData: state.appData,
       }
}
const mapDispatchToProps = dispatch => {
       return {
              filterPostDataAppendWebFilter: (wallID, timeStamp, postCount, networkId, after, heightEvent, tagName) => dispatch(filterPostDataAppendWebFilter(wallID, timeStamp, postCount, networkId, after, heightEvent, tagName))
       }
}
export default connect(mapStateToProps, mapDispatchToProps)(SocialBanner);