import React, { PureComponent, memo } from "react";


const generateColorDivs = (count) => {
  const colorClass = `tb_colr_ico tb_ico_path`;
  return Array.from({ length: count }, (_, i) => (
    <div key={i} className={`${colorClass}${i + 1}`}><div></div></div>
  ));
};

const networkIcons = {
  1: <div className="tb-twitter"></div>,
  2: <div className="tb-instagram-default"><div></div></div>,
  3: <div className="tb-facebook"><div></div></div>,
  4: <div className="tb-google-default">{generateColorDivs(4)}</div>,
  5: <div className="tb-pinterest"><div></div></div>,
  6: <div className="tb-flickr-default">{generateColorDivs(2)}</div>,
  7: <div className="tb-youtube-default">{generateColorDivs(2)}</div>,
  8: <div className="tb-vimeo"><div></div></div>,
  10: <div className="tb-linkedin"><div></div></div>,
  11: <div className="tb-tumblr"><div></div></div>,
  12: <div className="tb-rss"><div></div></div>,
  15: <div className="tb-workplace"><div></div></div>,
  18: <div className="tb-instagram-default"><div></div></div>,
  19: <div className="tb-yelp"><div></div></div>,
  20: <div className="tb-slack-default">{generateColorDivs(8)}</div>,
  21: <div className="tb-yammer"><div></div></div>,
  23: <div className="tb-airbnb"><div></div></div>,
  25: <div className="tb-soundcloud"><div></div></div>,
  26: <div className="tb-giphy-default">{generateColorDivs(7)}</div>,
  28: <div className="tb-tiktok-default">{generateColorDivs(7)}</div>,
  29: <div className="tb-onsite-upload"><div></div></div>,
  30: <div className="tb-vk"><div></div></div>,
  31: <div className="tb-tiktok-default">{generateColorDivs(7)}</div>,
  32: <div className="tb-vk"><div></div></div>,
  33: <div className="tb-trustpilot-default">{generateColorDivs(2)}</div>,
  34: <div className="tb-amazon-default">{generateColorDivs(2)}</div>,
  35: <div className="tb-tripadvisor"><div></div></div>,
  36: <div className="tb-tagembed-default">{generateColorDivs(3)}</div>,
  37: <div className="tb-aliexpress-default">{generateColorDivs(4)}</div>,
  38: <div className="tb-booking-default">{generateColorDivs(3)}</div>,
};

const DefNetIcon = memo(({ networkId, networkClass }) => {
  const networkClasses = `${networkClass} tb__icon tb_ico_default`;
  const icon = networkIcons[networkId] || '';

  return icon ? <div className={networkClasses}>{icon}</div> : '';
});

export default class DefaultNetwork extends PureComponent {
  render() {
    return <DefNetIcon {...this.props} />;
  }
}